body {
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, sans-serif;

}


.form-signin {
    width: 100%;
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
    margin-top: 5%;
}

.signin-logo{
  vertical-align: middle;
    border-style: none;
}

.label {
  display: inline-block;
  margin-bottom: .5rem;
}


.form-signin .form-control {
    position: relative;
    box-sizing: border-box;
    height: auto;
    padding: 10px;
    font-size: 16px;
}
.form-control {
    display: block;
    width: 100%;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  }

.form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }

.form-signin #username, .form-signin #location {
      margin-bottom: -1px;
      border-radius: 0;
  }

.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}



.form-signin #signin-email {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.form-signin #signin-password {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

@media only screen and (max-width: 700px) {
  select {
    border: 1px solid #999;
  }
}


.location-search {
  margin-bottom: 10px;
}
.inputField {
  margin-bottom: 10px;
}
 #search-btn{
   margin-bottom: 10px;
}

