.card-img {
  margin: 0 auto;
  width: 80%;
}

.card-title {
    margin: .75rem;
}

.card-text {
padding: 0px 5px 20px 5px
}

:root {
    --main-color: #a83f2a;
    --secondary-color: white;
    --main-text-color: #000;
    --secondary-text-color: #343a40;
    --send-message-form: #F5F5F5;
}

html, body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: system-ui;
    font-weight: 200;
    color: 	#3e5869;
}

#root {
    height: 100%;
}
.chat-container {
    display: grid;
    height: 100%;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 60px;
    grid-template-areas:
        "r m m m m m"
        "r m m m m m"
        "r m m m m m"
        "r m m m m m"
        "r m m m m m"
        "n f f f f f";
}

.new-room-form {
    grid-area: n;
}

.rooms-list {
    grid-area: r;
}

.message-list {
    grid-area: m;
}

.send-message-form {
    grid-area: f;
}

/* REST OF CSS */
.rooms-list {
    box-sizing: border-box;
    padding: 10px;
    background-color: #a83f2a;
    background-color: var(--main-color);
    overflow: scroll;
    height: 100%;
}

.rooms-list ul {
    list-style-type: none;
    padding: 0;
    overflow: scoll;
}

.rooms-list li {
    margin: 10px 0;
}

.rooms-list h3 {
    margin: 5px 0;
    color: white;
    color: var(--secondary-color);
}

.rooms-list .room a {
    color: #343a40;
    color: var(--secondary-text-color);
    font-weight: 600;
    text-decoration: none;

 }

.rooms-list .room.active a {
    color: white;
    color: var(--secondary-color);
}

.new-room-form {
    padding: 0 5px;
    background: white;
    background: var(--secondary-color);
    color: #000;
    color: var(--main-text-color);
}

.new-room-form form {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.new-room-form input {
    width: 135px;
    background: white;
    background: var(--secondary-color);
}

.new-room-form button {
    background: white;
    background: var(--secondary-color);
    color: #000;
    color: var(--main-text-color);
    border: 0;
}

.new-room-form input::-webkit-input-placeholder {
    color: #000;
    color: var(--main-text-color);
    font-weight: 200;
}

.new-room-form input::-ms-input-placeholder {
    color: #000;
    color: var(--main-text-color);
    font-weight: 200;
}

.new-room-form input::placeholder {
    color: #000;
    color: var(--main-text-color);
    font-weight: 200;
}

.new-room-form input:focus {
    outline-width: 0;
}

.new-room-form input {
    border: 0;
}

.new-room-form button {
    border: 0;
}

.message {
    margin: 15px 0;
}

.message .message-username {
    font-size: 11px;
    color: #000;
    color: var(--main-text-color);
    opacity: 0.9;
    margin-bottom: 6px;
}
.message .message-text {
    background: #a83f2a;
    background: var(--main-color);
    color: white;
    color: var(--secondary-color);
    display: inline;
    padding: 4px 8px;
    border-radius: 8px;
}

.message-list {
    box-sizing: border-box;
    padding-left: 6px;
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: white;
    background: var(--secondary-color);
}

.message-list .join-room {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-size: 34px;
    font-weight: 300;
}

.send-message-form {
    background: #F5F5F5;
    background: var(--send-message-form);
    display: flex;
}

.send-message-form input {
    width: 100%;
    padding: 15px 10px;
    margin: 0;
    border-style: none;
    background: #F5F5F5;
    background: var(--send-message-form);
    font-weight: 200;

}

.send-message-form input:focus {
    outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
    color: #000;
    color: var(--main-text-color);
}

.send-message-form input::-ms-input-placeholder {
    color: #000;
    color: var(--main-text-color);
}

.send-message-form input::placeholder {
    color: #000;
    color: var(--main-text-color);
}

.help-text {
    position: absolute;
    top: 10px;
}

@media only screen and (max-width: 700px){
  .chat-container {
      display: block;
      height: 100%;
  }
}

.App {
  background-color: #ffffff;
  margin: 0 auto;
  color: #000000;
  text-align: center;
}

.alpha {
  font-size: 5em;
  font-family: cursive;
  color: #ac3f1f;

}
.experience-label{
  margin-left: 2%;
}

@media only screen and (min-width: 700px) {
.App {
  background-color: #ffffff;
  color: #000000;
  text-align: center;
}

